interface contentObj {
  title: string;
  content: string;
  img: string;
  id: number;
  time: string;
}

@Component
export default class Content extends Vue {
  @Prop()
  cData!: contentObj;
  @Prop()
  index!: number;

  

  show() {
		console.log('goshow')
		uni.navigateTo({
		    url: `/pages/read/read?id=${this.cData.id}`
		});
		// uni.navigateTo({
		//     url: `pages/read/read/${this.cData.id}`
		// });
  }
}
